      体检也能省心又省力吗？
    </p>
    <p class="ani" swiper-animate-effect="zoomInLeft" swiper-animate-duration="0.5s" swiper-animate-delay="0.4s">
      我们愿意给您满意的答案。
    </p>
    <p class="ani" swiper-animate-effect="zoomInLeft" swiper-animate-duration="0.5s" swiper-animate-delay="0.8s">
      欲知详情，请咨询您的客户经理
    </p>
    <p class="ani" swiper-animate-effect="zoomInLeft" swiper-animate-duration="0.5s" swiper-animate-delay="0.8s">
      或垂询：<span>021-339702548</span>
    </p>
    <p class="ani" swiper-animate-effect="zoomInLeft" swiper-animate-duration="0.5s" swiper-animate-delay="0.8s">
      <span>400 889 4545</span>
    </p>
    <p class="ani" swiper-animate-effect="zoomInLeft" swiper-animate-duration="0.5s" swiper-animate-delay="1.2s">
      中智安心体检
    </p>
    <p class="ani" swiper-animate-effect="zoomInLeft" swiper-animate-duration="0.5s" swiper-animate-delay="1.2s">
