    </p>
    <p class="ani" swiper-animate-effect="zoomInLeft" swiper-animate-duration="0.5s" swiper-animate-delay="1.7s">
      <img src="../../../static/img/10_2.jpg" alt=""> 
    </p>    
  </swiper-slide>
</template>

<script>
import { swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'sweiperSlide11',
  components: {
    swiperSlide
  }
}
</script>

<style lang="stylus">
.page10
  background:#F7B93F url('static/img/004.png') 200% 40% no-repeat;
  background-size 65%
  padding 130px 0 0 95px
  box-sizing border-box
  p
    color cbai
    font-size 30px
    padding 0
    margin 20px 0 0 0

  p:nth-child(3)
    margin 60px 0 0 0
  p:nth-child(4)
    span 
      font-size 38px
  p:nth-child(5)
    font-size 38px
    margin-left 125px

  p:nth-child(6)
    margin 60px 0 0 0
  
  img
    width 262px
    height auto
    display block
    margin-top 60px

  

    
</style>



